<template>
    <div id="App" :class="{Closed : !openned}">
        <Document v-if="page == 'Document'" :logo="logo" :translate="translate" :NameResource="NameResource" :close="close" :creating="creating"/>
        <Edit v-else-if="page == 'Edit'" :infos_document="infos_document" :logo="logo" :translate="translate" :NameResource="NameResource" :close="close" :creating="creating"/>
        <ViewDocument v-else-if="page == 'ViewDocument'" :infos_document="infos_document" :logo="logo" :translate="translate" :NameResource="NameResource" :close="close" :creating="creating"/>
    </div>
</template>

<script>
import Document from './screens/Document';
import Edit from './screens/Edit';
import ViewDocument from './screens/ViewDocument';

export default {
    name:"App",
    components:{
        Document,
        Edit,
        ViewDocument
    },
    data: function(){
        return({
            page : "",

            infos_document:{
                date: "April 16, 2020",
                title: "SEARCH AND SEIZURE",
                text: "1. By this private instrument and in the form of law, THE POLICE DEPARTMENT, hereinafter referred to as REQUEST OF THE SEARCH AND SEIZURE\n\nCLAUSE 1: The Police Department requested a search and seizure warrant in the apartment of Mr. Flinst Oliver and Mr. Wayne Bruce, and also in their vehicles, after an anonymous report of a photo posted on the deep web channel, the individuals Flinst and Wayne were unmasked and armed, the day after they left the Police Department.\n\nCLAUSE 2: the request was granted, based on the aforementioned evidence, and the Police may invade the citizen's home for search and seizure.",
                images: [
                    
                ],
                signatures:[
                    {asign: "Example Name", info: "Judge 001"},
                ]
            },

            logo: "img/LegendaryBanner.png",

            translate:{
                TR_TITLE: "Judiciary Legendary",
                TR_EXIT : "Exit",
                TR_CREATE: "Create",
                TR_SAVE: "Save",
                TR_CANCEL: "Cancel",
                TR_ADD: "+ Signature",
                TR_REMOVE: "- Signature",
                TR_INSERT: "Click to insert a image",
                TR_BUTTON_INSERT: "INSERT",
                TR_EXAMPLE_NAME: "Example Name",
                TR_EXAMPLE_INFO: "Example Info"
            },

            NameResource: "Documents",
            openned : false,
            creating : false,
        })
    },
    methods:{
        OpenScreen: function(name){
            this.page = name;
        },

        close:function(){
            fetch(`https://${this.NameResource}/close`, {
                method: 'POST',
                body: JSON.stringify({
                })
            }); 

            this.page = "";
            this.openned = false;
        },

        keyPress: function(event){
            const key = event.key;
            
            if(key == "Escape"){
                this.close();
            }
        },

        receiveLua: function(event){
            if(!event || !event.data)
                return;
            
            const lua = event.data;

            if(lua.config){
                this.translate = lua.translate;
                this.NameResource = lua.NameResource;
                this.logo = lua.logo
            }

            else if(lua.openCreateModel){
                this.openned = true;
                this.OpenScreen('Document');
                this.creating = true;
            }

            else if(lua.openCreateDocument){
                this.openned = true;
                this.infos_document = lua.infos_document
                this.OpenScreen('Edit');
                this.creating = true;
            }

            else if(lua.openEditModel){
                this.openned = true;
                this.infos_document = lua.infos_document
                this.OpenScreen('Edit');
                this.creating = false;
            }

            else if(lua.openEditDocument){
                this.openned = true;
                this.infos_document = lua.infos_document
                this.OpenScreen('Edit');
                this.creating = false;
            }

            else if(lua.openViewDocument){
                this.openned = true;
                this.infos_document = lua.infos_document
                this.OpenScreen('ViewDocument');
                this.creating = false;
            }
        }
    },

    created: function(){
        window.addEventListener('message', this.receiveLua);  
        window.addEventListener('keydown', this.keyPress);
    },
    destroyed: function(){
        window.removeEventListener('message', this.receiveLua);
        window.removeEventListener('keydown', this.keyPress);
    }
}
</script>

<style>
    .Closed{
        display: none !important;
    }
</style>