<template>
    <div>
        <div class="DocumentBox">
            <div class="DocumentButtonsBox">
                <div class="DocumentButtons">
                    <p class="DocumentButtonsText">{{translate.TR_CREATE}}</p>

                    <div class="DocumentButtonsImageBox">
                        <img src="img/checked.png" alt="" class="DocumentButtonsImage" v-on:click="saveModel">    
                    </div>   
                </div>    
                <div class="DocumentButtons">
                    <p class="DocumentButtonsText">{{translate.TR_CANCEL}}</p>
                    
                    <div class="DocumentButtonsImageBox">
                        <img src="img/cancel.png" alt="" class="DocumentButtonsImage" v-on:click="close">    
                    </div>   
                </div>    
                <div class="DocumentButtons">
                    <p class="DocumentButtonsText">{{translate.TR_ADD}}</p>
                    
                    <div class="DocumentButtonsImageBox">
                        <img src="img/add.png" alt="" class="DocumentButtonsImage" v-on:click="insertSigniture">    
                    </div>   
                </div>    
                <div class="DocumentButtons">
                    <p class="DocumentButtonsText">{{translate.TR_REMOVE}}</p>
                    
                    <div class="DocumentButtonsImageBox">
                        <img src="img/remove.png" alt="" class="DocumentButtonsImage" v-on:click="removeSigniture">    
                    </div>   
                </div>    
            </div>

            <div class="DocumentPageBox">
                <div class="PageHeaderBox">
                    <img src="img/canto.png" alt="" class="PageHeaderImage">
                </div>
                <div class="PageFooterBox">
                    <img src="img/rodape.png" alt="" class="PageFooterImage">
                </div>
                <div class="DocumentTitleBox">
                    <div class="DocumentTitleImageBox">
                        <img :src="logo" alt="" class="DocumentTitleImage">
                    </div>

                    <p class="DocumentTitleText">{{translate.TR_TITLE}}</p>
                    <input type="text" class="DocumentTitleDate" v-model="infos_document.date" spellcheck="false">
                </div>
                <div class="DocumentTextBox">
                    <div class="DocumentTextTitleBox">
                        <input type="text" class="DocumentTextTitle" v-model="infos_document.title" spellcheck="false">
                    </div>

                    <div class="DocumentTextLiteralBox">
                        <textarea class="DocumentTextLiteral" ref="literaltext" v-on:input="autoResize" v-model="infos_document.text" spellcheck="false">

                        </textarea>
                    </div>
                </div>
            </div>

            <div class="DocumentPageBox">
                <div class="PageHeaderBox">
                    <img src="img/canto.png" alt="" class="PageHeaderImage">
                </div>
                
                <div class="PageFooterBox">
                    <img src="img/rodape.png" alt="" class="PageFooterImage">
                </div>

                <div class="DocumentImagesBox"
                    v-for="(item, index) in infos_document.images"
                    :key="index"
                >
                    <div class="DocumentImagesDeleteBox">
                        <img src="img/delete.png" alt="" class="DocumentImagesDelete"
                            v-on:click="deleteImage(index)"
                        >
                    </div>
                    <img :src="item" alt="" class="DocumentImages">
                    <p></p>
                </div>
                
                <div class="DocumentImagesBox">
                    <div class="DocumentInsertImageBox" :class="{DocumentInsertImageBoxEnable: InsertImageEnabled}">
                        <input type="text" class="DocumentInsert" v-model="inputImage" placeholder="URL IMAGE with .png .gif or .jpg" spellcheck="false">
                        <p class="DocumentInsertButton" v-on:click="insertImage">{{translate.TR_BUTTON_INSERT}}</p>
                    </div>
                    <img src="img/insertimg.png" alt="" class="DocumentImages" v-on:click="enableInsertImage(true)">
                    <p>{{translate.TR_INSERT}}</p>
                </div>
            </div>
            <div class="DocumentPageBox">
                <div class="PageHeaderBox">
                    <img src="img/canto.png" alt="" class="PageHeaderImage">
                </div>
                
                <div class="PageFooterBox">
                    <img src="img/rodape.png" alt="" class="PageFooterImage">
                </div>

                <div class="DocumentAllAsignsBox">
                    <div class="DocumentAsignBox"
                        v-for="(item, index) in infos_document.signatures"
                        :key="index"
                    >
                        <input type="text" class="DocumentAsign" v-model="item.asign" spellcheck="false">
                        <input type="text" class="DocumentInfoAsign" v-model="item.info" spellcheck="false">
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props:{
        logo:{},
        translate:{},
        NameResource:{},
        close:{},
        creating:{},
    },
    data: function(){
        return({
            infos_document:{
                date: "April 16, 2020",
                title: "SEARCH AND SEIZURE",
                text: "1. By this private instrument and in the form of law, THE POLICE DEPARTMENT, hereinafter referred to as REQUEST OF THE SEARCH AND SEIZURE\n\nCLAUSE 1: The Police Department requested a search and seizure warrant in the apartment of Mr. Flinst Oliver and Mr. Wayne Bruce, and also in their vehicles, after an anonymous report of a photo posted on the deep web channel, the individuals Flinst and Wayne were unmasked and armed, the day after they left the Police Department.\n\nCLAUSE 2: the request was granted, based on the aforementioned evidence, and the Police may invade the citizen's home for search and seizure.",
                images: [
                    
                ],
                signatures:[
                    {asign: "Example Name", info: "Judge 001"},
                ]
            },

            inputImage: "",
            InsertImageEnabled: false,
        })
    },
    methods:{
        autoResize: function (){
            const el = this.$refs.literaltext;
            // el.style.height = 'fit-content';
            el.style.height = (el.scrollHeight)+'px';

            // el.rows = "100";
        },

        deleteImage:function(index){
            this.infos_document.images.splice(index, 1);
        },

        enableInsertImage: function(value){
            this.InsertImageEnabled = value;
        },

        insertImage: function(){
            if(this.inputImage.includes('.png') || this.inputImage.includes('.jpg') || this.inputImage.includes('.gif')){
                this.infos_document.images.push(this.inputImage);
                this.inputImage = "";
                this.enableInsertImage(false);
            }
        },

        insertSigniture: function(){
            this.infos_document.signatures.push({asign: this.translate.TR_EXAMPLE_NAME, info: this.translate.TR_EXAMPLE_INFO});
        },

        removeSigniture: function(){
            if( this.infos_document.signatures.length > 0 )
                this.infos_document.signatures.pop();
        },

        saveModel: function(){
            fetch(`https://${this.NameResource}/saveModel`, {
                method: 'POST',
                body: JSON.stringify({
                    infos_document: this.infos_document,
                    creating : this.creating
                })
            }); 

            this.close();
        }
    },
    mounted: function(){
        this.autoResize();
    }
}
</script>

